import ExtensionsHeader from "@components/extensions-header";
import { useEffect, useState } from "react";
import { FormattedMessage, injectIntl } from "react-intl";
import Select from "react-select";
import Sidebar from "./Sidebar";
import { columns } from "./columns";
import { useDispatch, useSelector } from "react-redux";
import DataTable from "react-data-table-component";
import { ChevronDown } from "react-feather";
import ReactPaginate from "react-paginate";
import {
  Button,
  Card,
  CardBody,
  CardHeader,
  CardTitle,
  Col,
  Input,
  Row,
} from "reactstrap";

import "@styles/react/libs/react-select/_react-select.scss";
import "@styles/react/libs/tables/react-dataTable-component.scss";
import "@styles/react/libs/flatpickr/flatpickr.scss";
import Flatpickr from "react-flatpickr";
import { toast } from "react-toastify";
import { getData, getDataExport } from "../store/action";
import XLSX from "xlsx";

import { getData as getDataProduct } from "../../../product/store/action";
import { getData as getDataSection } from "../../../product/term/store/action";

import { getData as getDataInstructors } from "../../../instructors/store/action";

const CustomHeader = ({ toggleSidebar, exportToExcel }) => {
  return (
    <div className="invoice-list-table-header w-100 mr-1 ml-50 mt-1 mb-75">
      <Row className="justify-content-end">
        <Col
          xl="6"
          className="d-flex align-items-sm-center justify-content-lg-end justify-content-start flex-lg-nowrap flex-wrap flex-sm-row flex-column gap-3 pr-lg-1 p-0 mt-lg-0 mt-1"
        >
          <Button.Ripple color="primary" onClick={exportToExcel}>
            Export Excel
          </Button.Ripple>
          <Button.Ripple color="primary" onClick={toggleSidebar}>
            <FormattedMessage id="Add" />
          </Button.Ripple>
        </Col>
      </Row>
    </div>
  );
};

const CourseTimeTableList = ({ intl }) => {
  const dispatch = useDispatch();
  const store = useSelector((state) => state.timeTable);
  const product = useSelector((state) => state?.products?.data);
  const section = useSelector((state) => state?.terms?.data);
  const instructors = useSelector((state) => state?.instructors?.allData);
  const [searchTerm, setSearchTerm] = useState("");
  const [currentPage, setCurrentPage] = useState(1);
  const [rowsPerPage, setRowsPerPage] = useState(20);
  const [sidebarOpen, setSidebarOpen] = useState(false);
  const [fromDate, setFromDate] = useState();
  const [disable, setDisable] = useState(false);
  const lang = useSelector((state) => state.common.language);
  const [filterStatus, setFilterStatus] = useState({
    value: "",
    label: <FormattedMessage id="status" />,
  });

  const optionProduct = {
    value: "",
    label: <FormattedMessage id="Select Course" />,
  };
  const optionSection = {
    value: null,
    label: <FormattedMessage id="Select term" />,
  };
  const optionInstructor = {
    value: null,
    label: <FormattedMessage id="Select Instructor" />,
  };

  const [filterProduct, setFilterProduct] = useState(optionProduct);
  const [filterSection, setFilterSection] = useState(optionSection);
  const [filterInstructor, setFilterInstructor] = useState(optionInstructor);
  const [searchName, setSearchName] = useState(null);
  const [searchClass, setSearchClass] = useState(null);

  const [loadData, setLoadData] = useState(false);
  const toggleSidebar = () => {
    setSidebarOpen(!sidebarOpen);
    setDisable(false);
  };

  useEffect(() => {
    dispatch(
      getData({
        filter: {
          // curriculumSectionId: 1,
        },
        skip: 0,
        limit: 20,
        order: [
          {
            key: "status",
            value: "desc",
          },
          // {
          //   key: "createdAt",
          //   value: "asc",
          // },
        ],
      })
    );
    dispatch(
      getDataExport({
        filter: {},
        order: [
          {
            key: "createdAt",
            value: "desc",
          },
        ],
      })
    );

    dispatch(
      getDataProduct({
        filter: {
          status: 1,
          product_type: "0",
          lang: lang,
        },
        // skip: 0,
        // limit: 20,
        order: [
          {
            key: "status",
            value: "DESC",
          },
        ],
      })
    );
    dispatch(
      getDataSection({
        filter: {
          status: 1,
          lang: lang,
          productId:
            filterProduct?.value != "" ? filterProduct?.value : undefined,
        },
        skip: 0,
        limit: 1000000000,
        order: [
          {
            key: "id",
            value: "asc",
          },
        ],
      })
    );

    dispatch(
      getDataInstructors({
        filter: {
          lang: lang,
          // status: 1,
        },
        limit: 50,
        skip: 0,
        order: [
          {
            key: "id",
            value: "asc",
          },
        ],
      })
    );
  }, [dispatch, filterProduct?.value]);

  useEffect(() => {
    dispatch(
      getData({
        filter: {
          curriculumSectionId: filterSection.value || undefined,
          instructorId: filterInstructor.value || undefined,
          productId: filterProduct.value || undefined,
          status: filterStatus.value || undefined,
          class: searchClass || undefined,
          date: fromDate || undefined,
        },
        skip: (currentPage - 1) * rowsPerPage,
        limit: rowsPerPage,
        order: [
          {
            key: "status",
            value: "desc",
          },
          {
            key: "id",
            value: "asc",
          },
        ],
      })
    );
  }, [loadData, currentPage, rowsPerPage, lang]);

  useEffect(() => {
    switch (store?.type) {
      case "ADD_TIMETABLE":
        if (store?.status == 200) {
          toast.success(<FormattedMessage id={"successfully added new!"} />);
          setLoadData(!loadData);
          toggleSidebar();
        } else if (store?.status == 400) {
          toast.warn(<FormattedMessage id={"Add new failure!"} />);
        }
        break;
      // case "DELETE_TIMETABLE":
      //   if (store?.status == 200) {
      //     toast.success(<FormattedMessage id={"Delete successfully!"} />);
      //     setLoadData(!loadData);
      //   } else if (store?.status == 400) {
      //     toast.warn(<FormattedMessage id={"Delete failed!"} />);
      //   }

      //   break;
      case "UPDATE_TIMETABLE":
        if (store?.status == 200) {
          toast.success(<FormattedMessage id={"Update successful!"} />);
          setLoadData(!loadData);
        } else if (store?.status == 400) {
          toast.warn(<FormattedMessage id={"Update failed!"} />);
        }
        break;
      default:
        break;
    }
  }, [store.status]);

  const statusOptions = [
    { value: "", label: <FormattedMessage id="Select status" /> },
    // { value: "0", label: <FormattedMessage id="Deactive" /> },
    { value: "1", label: <FormattedMessage id="Active" /> },
    { value: "0", label: <FormattedMessage id="Blocked" /> },
  ];

  const handlePerPage = (e) => {
    const value = parseInt(e.currentTarget.value);
    setRowsPerPage(value);
  };
  const count = Number(Math.ceil(store.total / rowsPerPage));

  const dataToRender = () => {
    if (store?.data?.length > 0) {
      return store?.data;
    }
  };
  const isSearchable = (event) => {
    console.log(event.target.value);
  };

  const exportToExcel = () => {
    if (store?.dataExport?.length > 0) {
      const data = store?.dataExport;
      const cols = [
        { header: "ID", key: "id" },
        { header: "Kho hc", key: "product" },
        { header: "Ging vin", key: "instructor" },
        { header: "Lp", key: "class" },
        { header: "Phng", key: "room" },
        { header: "Gi", key: "time" },
        { header: "Ngy", key: "date" },
        { header: "Link", key: "link" },
        { header: "Ghi ch", key: "note" },
        { header: "Trng thi", key: "status" },
        { header: "Ngn ng", key: "lang" },
        { header: "Ngy to", key: "createdAt" },
        { header: "Ngy cp nht", key: "updatedAt" },
      ];
      const mappedData = data.map((item) =>
        Object.fromEntries(
          Object.entries(item).map(([key, val]) => {
            if (key === "instructor" && val) {
              return [
                cols.find((col) => col.key === key)?.header || key,
                val?.first_name &&
                  val?.last_name &&
                  val?.first_name + " " + val?.last_name,
              ];
            }
            if (key === "product" && val) {
              return [
                cols.find((col) => col.key === key)?.header || key,
                val?.product_names?.length > 0 && val?.product_names[0]?.name,
              ];
            }
            return [cols.find((col) => col.key === key)?.header || key, val];
          })
        )
      );
      const worksheet = XLSX.utils.json_to_sheet(mappedData, {
        header: cols.map((col) => col.header),
      });
      const workbook = XLSX.utils.book_new();
      XLSX.utils.book_append_sheet(workbook, worksheet, "Thi kho biu");
      XLSX.writeFile(workbook, "timetable.xlsx");
    }
  };

  return (
    <div className="app-user-list">
      <ExtensionsHeader title={<FormattedMessage id="Timetable" />} />
      <Card>
        <CardBody>
          <Row>
            <Col md="4">
              <Select
                isClearable={false}
                className="react-select"
                classNamePrefix="select"
                options={[
                  optionProduct,
                  ...product?.map((item, index) => {
                    return {
                      value: item?.id,
                      label: item?.product_names[0]?.name || "",
                    };
                  }),
                ]}
                value={filterProduct}
                onChange={(data) => {
                  setFilterProduct(data);
                }}
              />
            </Col>

            <Col md="4">
              <Select
                isClearable={false}
                className="react-select"
                classNamePrefix="select"
                options={[
                  optionSection,
                  ...section?.map((item, index) => {
                    return {
                      value: item?.id,
                      label: item?.title || "",
                    };
                  }),
                ]}
                value={filterSection}
                onChange={(data) => {
                  setFilterSection(data);
                }}
              />
            </Col>

            <Col md="4">
              <Select
                isClearable={false}
                className="react-select"
                classNamePrefix="select"
                options={[
                  optionInstructor,
                  ...instructors?.map((item, index) => {
                    return {
                      value: item?.id && item?.id,
                      label: item.first_name + " " + item.last_name || "",
                    };
                  }),
                ]}
                value={filterInstructor}
                onChange={(data) => {
                  setFilterInstructor(data);
                }}
              />
            </Col>

            <Col md="4">
              <Input
                id="search-invoice"
                className=" w-100"
                placeholder={intl.formatMessage({ id: "class" })}
                type="text"
                value={searchClass}
                onChange={(e) => {
                  setSearchClass(e.target.value);
                }}
              />
            </Col>
            <Col md="4">
              <Flatpickr
                value={fromDate}
                placeholder={intl.formatMessage({ id: "school day" })}
                id="date-time-picker"
                className="form-control"
                options={{ enableTime: false }}
                onChange={(date) => {
                  const dateObj = new Date(date[0]);

                  const year = dateObj.getFullYear();
                  const month = ("0" + (dateObj.getMonth() + 1)).slice(-2);
                  const dateSelect = ("0" + dateObj.getDate()).slice(-2);

                  const formattedDate = `${year}-${month}-${dateSelect}`;
                  setFromDate(formattedDate);
                }}
              />
            </Col>
            <Col md="4">
              <Select
                isClearable={false}
                className="react-select"
                classNamePrefix="select"
                options={statusOptions}
                value={filterStatus}
                onChange={(data) => {
                  setFilterStatus(data);
                }}
              />
            </Col>
            <Col md="4">
              <Button
                color="primary"
                onClick={() => {
                  setLoadData(!loadData);
                  setCurrentPage(1);
                }}
              >
                <FormattedMessage id="Search" />
              </Button>
            </Col>
          </Row>
        </CardBody>
      </Card>
      <Card>
        <DataTable
          noHeader
          subHeader
          responsive
          highlightOnHover
          noDataComponent={
            <div className="sc-fznWqX gnahTY">
              <div className="sc-AxjAm gIMaKV rdt_Table" role="table">
                <div className="sc-fzqARJ icdHOq">
                  <div
                    style={{
                      padding: "25px ",
                      textAlign: "center",
                      color: "black",
                      background: "white",
                    }}
                  >
                    <FormattedMessage id={"There are no records to display"} />
                  </div>
                </div>
              </div>
            </div>
          }
          paginationServer
          columns={columns}
          sortIcon={<ChevronDown />}
          className="react-dataTable"
          data={dataToRender()}
          subHeaderComponent={
            <CustomHeader
              toggleSidebar={toggleSidebar}
              handlePerPage={handlePerPage}
              rowsPerPage={rowsPerPage}
              searchTerm={searchTerm}
              exportToExcel={exportToExcel}
            />
          }
        />
      </Card>
      {count > 0 && (
        <ReactPaginate
          previousLabel={""}
          nextLabel={""}
          pageCount={count || 0}
          activeClassName="active"
          forcePage={currentPage !== 0 ? currentPage - 1 : 0}
          onPageChange={(page) => setCurrentPage(page.selected + 1)}
          pageClassName={"page-item"}
          nextLinkClassName={"page-link"}
          nextClassName={"page-item next"}
          previousClassName={"page-item prev"}
          previousLinkClassName={"page-link"}
          pageLinkClassName={"page-link"}
          containerClassName={
            "pagination react-paginate justify-content-center my-2 pr-1"
          }
        />
      )}
      <Sidebar
        open={sidebarOpen}
        toggleSidebar={toggleSidebar}
        {...{
          disable,
          setDisable,
        }}
      />
    </div>
  );
};

export default injectIntl(CourseTimeTableList);
