import Sidebar from "@components/sidebar";
import classnames from "classnames";
import { getData as getDataSection } from "../../../product/term/store/action";
import { useEffect, useState } from "react";
import { Controller, useForm } from "react-hook-form";
import { FormattedMessage, injectIntl } from "react-intl";
import "react-phone-number-input/style.css";
import { useDispatch, useSelector } from "react-redux";
import { Button, Form, FormGroup, Input, Label, Media, Row } from "reactstrap";

import { yupResolver } from "@hookform/resolvers/yup";
import * as yup from "yup";
import Select from "react-select";
import { addTimeTable } from "../store/action";

const SidebarNewCourseTimeTable = ({
  open,
  toggleSidebar,
  intl,
  disable,
  setDisable,
}) => {
  const store = useSelector((state) => state.timeTable);

  const [changeSelect, setChangeSelect] = useState();
  const product = useSelector((state) => state?.products?.data);
  const section = useSelector((state) => state?.terms?.data);
  const instructors = useSelector((state) => state?.instructors?.allData);
  const [filterProduct, setFilterProduct] = useState(undefined);
  const lang = useSelector((state) => state.common.language);
  console.log(filterProduct);
  const dispatch = useDispatch();
  console.log(changeSelect);
  useEffect(() => {
    dispatch(
      getDataSection({
        filter: {
          status: 1,
          lang: lang,
          productId: filterProduct,
        },
        skip: 0,
        limit: 1000000000,
        order: [
          {
            key: "id",
            value: "asc",
          },
        ],
      })
    );
  }, [dispatch, filterProduct]);

  const cate = yup.object({
    productId: yup
      .number()
      .required(<FormattedMessage id="The course field is required" />),
    curriculumSectionId: yup
      .number()
      .required(<FormattedMessage id="The term field is required" />),
    time: yup
      .string()
      .required(<FormattedMessage id="The time study field is required" />),
    date: yup
      .string()
      .required(<FormattedMessage id="The school day field is required" />),
    class: yup
      .string()
      .required(<FormattedMessage id="The class field is required" />),
    room: yup
      .string()
      .required(<FormattedMessage id="The room field is required" />),
    instructorId: yup
      .number()
      .required(<FormattedMessage id="The instructors field is required" />),
  });
  const {
    register,
    formState: { errors },
    handleSubmit,
    control,
    setError,
    getValues,
    setValue,
  } = useForm({
    resolver: yupResolver(cate),
    mode: "all",
  });

  useEffect(() => {
    if (store?.err?.statusCode) {
      setDisable(false);
    }
  }, [store?.err]);

  const onSubmit = async (values) => {
    try {
      dispatch(
        addTimeTable({
          productId: values?.productId || 0,
          curriculumSectionId: values?.curriculumSectionId || 0,
          date: values?.date || "",
          class: values?.class || "",
          room: values?.room || "",
          link: values?.link || " ",
          instructorId: values?.instructorId || "",
          time: values?.time || "",
          note: values?.note || " ",
          lang: lang,
          status: parseInt(values?.status),
        })
      );
    } catch (error) {
      console.log(error);
    }
  };

  return (
    <Sidebar
      size="lg"
      open={open}
      title={<FormattedMessage id="Add New TimeTable" />}
      headerClassName="mb-1"
      contentClassName="pt-0"
      toggleSidebar={toggleSidebar}
    >
      <Form onSubmit={handleSubmit(onSubmit)}>
        <FormGroup>
          <Label>
            <FormattedMessage id="Course" />
            <span className="text-danger"> * </span>
          </Label>
          <Controller
            control={control}
            name="productId"
            render={({ field }) => {
              return (
                <Select
                  id="productId"
                  innerRef={register}
                  name="productId"
                  placeholder={""}
                  className={classnames(
                    "react-select",
                    !changeSelect
                      ? {
                          "is-invalid": errors["productId"],
                        }
                      : ""
                  )}
                  options={product?.map((item, index) => {
                    return {
                      value: item?.id,
                      label: item?.product_names[0]?.name || "",
                      number: index + 1,
                    };
                  })}
                  classNamePrefix="select"
                  {...field}
                  onChange={(e) => {
                    setError("productId", "");
                    setValue("productId", e?.value);
                    setChangeSelect(e?.value);
                    setFilterProduct(e?.value);
                  }}
                />
              );
            }}
          ></Controller>
          <small className="text-danger">
            {errors?.productId && errors.productId.message}
          </small>
        </FormGroup>

        <FormGroup>
          <Label>
            <FormattedMessage id="term" />
            <span className="text-danger"> * </span>
          </Label>
          <Controller
            control={control}
            name="curriculumSectionId"
            render={({ field }) => {
              return (
                <Select
                  id="curriculumSectionId"
                  innerRef={register}
                  name="curriculumSectionId"
                  placeholder={""}
                  className={classnames(
                    "react-select",
                    !changeSelect
                      ? {
                          "is-invalid": errors["curriculumSectionId"],
                        }
                      : ""
                  )}
                  options={section?.map((item, index) => {
                    return {
                      value: item?.id,
                      label: item?.title || "",
                      number: index + 1,
                    };
                  })}
                  classNamePrefix="select"
                  {...field}
                  onChange={(e) => {
                    setError("curriculumSectionId", "");
                    setValue("curriculumSectionId", e?.value);
                    setChangeSelect(e?.value);
                  }}
                />
              );
            }}
          ></Controller>
          <small className="text-danger">
            {errors?.curriculumSectionId && errors.curriculumSectionId.message}
          </small>
        </FormGroup>

        <FormGroup>
          <Label for="time">
            <FormattedMessage id="time" />{" "}
            <span className="text-danger">*</span>
          </Label>
          <Input
            name="time"
            id="time"
            type="time"
            placeholder=""
            innerRef={register}
            onBlur={() => {
              let time = document.getElementById("time");
              if (time && time.value) {
                time.value = time.value.trim();
              }
            }}
            className={classnames({ "is-invalid": errors["time"] })}
          />
          <small className="text-danger">
            {errors?.time && errors.time.message}
          </small>
          {errors?.time?.type == "validate" && (
            <small className="text-danger">
              <FormattedMessage id="Invalid name" />
            </small>
          )}
        </FormGroup>

        <FormGroup>
          <Label for="date">
            <FormattedMessage id="school day" />{" "}
            <span className="text-danger">*</span>
          </Label>
          <Input
            name="date"
            id="date"
            type="date"
            placeholder=""
            innerRef={register}
            onBlur={() => {
              let date = document.getElementById("date");
              if (date && date.value) {
                date.value = date.value.trim();
              }
            }}
            className={classnames({ "is-invalid": errors["date"] })}
          />
          <small className="text-danger">
            {errors?.date && errors.date.message}
          </small>
          {errors?.date?.type == "validate" && (
            <small className="text-danger">
              <FormattedMessage id="Invalid name" />
            </small>
          )}
        </FormGroup>

        <FormGroup>
          <Label for="class">
            <FormattedMessage id="class" /> <span class="text-danger">*</span>
          </Label>
          <Input
            name="class"
            id="class"
            type="class"
            placeholder=""
            innerRef={register}
            onBlur={() => {
              let className = document.getElementById("class");
              if (className && className.value) {
                className.value = className.value.trim();
              }
            }}
            className={classnames({ "is-invalid": errors["class"] })}
          />
          <small className="text-danger">
            {errors?.class && errors.class.message}
          </small>
          {errors?.class?.type == "validate" && (
            <small className="text-danger">
              <FormattedMessage id="Invalid name" />
            </small>
          )}
        </FormGroup>

        <FormGroup>
          <Label for="room">
            <FormattedMessage id="room" /> <span class="text-danger">*</span>
          </Label>
          <Input
            name="room"
            id="room"
            type="room"
            placeholder=""
            innerRef={register}
            onBlur={() => {
              let room = document.getElementById("room");
              if (room && room.value) {
                room.value = room.value.trim();
              }
            }}
            className={classnames({ "is-invalid": errors["room"] })}
          />
          <small className="text-danger">
            {errors?.room && errors.room.message}
          </small>
          {errors?.room?.type == "validate" && (
            <small className="text-danger">
              <FormattedMessage id="Invalid name" />
            </small>
          )}
        </FormGroup>

        <FormGroup>
          <Label>
            <FormattedMessage id="Instructors" />
            <span className="text-danger"> * </span>
          </Label>
          <Controller
            control={control}
            name="instructorId"
            render={({ field }) => {
              return (
                <Select
                  id="instructorId"
                  innerRef={register}
                  name="instructorId"
                  placeholder={<FormattedMessage id="Select" />}
                  className={classnames(
                    "react-select",
                    !changeSelect
                      ? {
                          "is-invalid": errors["instructorId"],
                        }
                      : ""
                  )}
                  options={instructors?.map((item, index) => {
                    return {
                      value: item?.id && item?.id,
                      label: item.first_name + " " + item.last_name || "",
                    };
                  })}
                  classNamePrefix="select"
                  {...field}
                  onChange={(e) => {
                    setError("instructorId", "");
                    setValue("instructorId", e?.value);
                    setChangeSelect(e?.value);
                  }}
                />
              );
            }}
          ></Controller>
          <small className="text-danger">
            {errors?.instructorId && errors.instructorId.message}
          </small>
        </FormGroup>

        <FormGroup>
          <Label for="link">
            <FormattedMessage id="Add link" />
          </Label>

          <Input name="link" id="link" placeholder="" innerRef={register} />
        </FormGroup>

        <FormGroup>
          <Label for="note">
            <FormattedMessage id="Note" />
          </Label>
          <Input name="note" id="note" placeholder="" innerRef={register} />
        </FormGroup>

        <FormGroup>
          <Label for="status">
            <FormattedMessage id="status" />{" "}
          </Label>

          <Input
            type="select"
            name="status"
            id="status"
            innerRef={register({ required: true })}
          >
            <option value="1">{intl.formatMessage({ id: "Active" })}</option>
            <option value="0">{intl.formatMessage({ id: "Deactive" })}</option>

            <option value="2">{intl.formatMessage({ id: "Blocked" })}</option>
          </Input>
        </FormGroup>

        <div style={{ textAlign: "end" }}>
          <Button
            type="submit"
            className="mr-1"
            color="primary"
            disabled={disable}
          >
            <FormattedMessage id="Save" />
          </Button>
          <Button
            type="reset"
            color="secondary"
            outline
            onClick={toggleSidebar}
          >
            <FormattedMessage id="Cancel" />
          </Button>
        </div>
      </Form>
    </Sidebar>
  );
};

export default injectIntl(SidebarNewCourseTimeTable);
